ks-cart-deal-image {
    display: block;
    position: relative;
    box-sizing: border-box;

    width: 180px;
    height: 180px;

    margin-right: 20px;

    transform: translateX(-20px);
}

ks-cart-deal-image ks-img3 {
    width: 100%;
    height: 100%;
}

ks-cart-deal-image .circle {
    position: absolute;
    box-sizing: border-box;
    overflow: hidden;

    width: 145px;
    height: 145px;
    padding: 10px;

    border-radius: 100%;
    background-color: white;
}

ks-cart-deal-image .center {
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

ks-cart-deal-image .fx {
    position: absolute;

    width: 250px;
    height: 250px;

    max-width: initial;
    animation-name: rotate;
    animation-iteration-count: infinite;
    animation-timing-function: linear;
}

ks-cart-deal-image .fx1 {
    animation-duration: 50s;
}

ks-cart-deal-image .fx2 {
    animation-duration: 40s;
    animation-direction: reverse;
}

ks-cart-deal-image .fx3 {
    animation-duration: 40s;
}

ks-cart-deal-image .fx4 {
    animation-duration: 50s;
    animation-direction: reverse;
}

@keyframes rotate {
    from {transform: translate(-50%, -50%) rotate(0deg)}
    to {transform: translate(-50%, -50%) rotate(360deg)}
}

@media only screen and (max-width: 760px) {
    ks-cart-deal-image {
        transform: translateX(0px);
        margin: 10px auto 30px auto;
    }
}