ks-cart-deal {
    display: flex;
    overflow: hidden;

    justify-content: center;
    align-items: center;

    background-color: #ffffff;
    padding: 0px 30px;
    box-sizing: border-box;
}

ks-cart-deal .text {
    margin: 30px 0;
    font-family: var(--font-emphasis);
    text-align: left;

    display: flex;
    flex-direction: column;
    gap: 5px;
}

ks-cart-deal .name {
    font-size: 20px;
    font-weight: 700;
}

ks-cart-deal .prices {
    display: flex;
    align-items: center;
    gap: 10px;
}

ks-cart-deal .bottom {
    display: flex;
    align-items: center;
    gap: 10px;

    font-size: 38px;
    line-height: 38px;
    font-weight: 700;
    color: var(--color-secondary);
    font-family: var(--font-emphasis);
}

ks-cart-deal .bottom s {
    color: black;
    font-size: 28px;
}

ks-cart-deal ks-button > * {
    padding-left: 40px;
    padding-right: 40px;
}

ks-cart-deal .variants label {
    display: inline-block;
    font-size: 15px;
    flex-shrink: 0;
    margin-right: 10px;
}

ks-cart-deal .variants ks-icon {
    transform: translateX(-30px);
    margin-right: -30px;
    pointer-events: none;
}

ks-cart-deal select {
    display: inline-block;
    padding: 8px 40px 10px 10px;
    height: 36px;
    min-width: 200px;

    font-size: 15px;
    appearance: none;

    background-color: #ffffff;
    border: 1px solid #d9d9d9;
    
    transition: background-color 0.3s ease;
}

ks-cart-deal select::-ms-expand {
	display: none;
}

ks-cart-deal select:hover {
    background-color: #e6e6e6;
}

ks-cart-deal select option {
    font-weight: normal;
}

@media only screen and (max-width: 760px) {
    ks-cart-deal {
        flex-direction: column;
        padding: 20px;
    }

    ks-cart-deal .variants {
        justify-content: center;
    }

    ks-cart-deal .text {
        text-align: center;
        gap: 10px;
        margin: 0;
    }

    ks-cart-deal .prices {
        display: block;
    }

    ks-cart-deal select {
        max-width: 220px;
        text-align: center;
        text-align-last: center;
    }

    ks-cart-deal .bottom {
        flex-wrap: wrap;
        justify-content: center;
    }
}